import ApolloClient, {InMemoryCache} from 'apollo-boost';
import gql from 'graphql-tag';

export const feedbackRequestHandler = function () {

  const client = new ApolloClient({
    uri: '/api/2.0/graphql/index',
    cache: new InMemoryCache()
  });

  const request = (opts) => client.mutate({
    mutation: gql`
      mutation sendTelegramFeedback (
        $name: String,
        $email: String,
        $phone: String,
        $other: String
      ){
        contact {
          sendTelegramFeedback(
            name: $name,
            email: $email,
            phone: $phone,
            other: $other
          ){
            ... on Result {
              success
            }
            ... on ValidationErrorsList {
              success
              errorCode
              errors {
                field
                messages
              }
            }
          }
        }
      }
    `,
    variables: {
      name: opts.name || null,
      email: opts.email || null,
      phone: opts.phone || null,
      other: null
    },
  })
  .catch(error => console.error(error));

  return {
    submit: (values, callback) => {
      request(values).then(result => callback.call(null, result));
    }
  }
}
