export const tariffsHandler = function () {
  const tariffsSwitch = document.querySelector('.tariffs__switch-input');
  const tariffBusiness = document.querySelector('.tariffs__tariff-price-value-business');
  const tariffPremium = document.querySelector('.tariffs__tariff-price-value-premium');
  const tariffsDiscountNotification = document.querySelector('.tariffs__switch-block-discount');

  if (tariffsSwitch.checked) {
    tariffBusiness.textContent = '4 490 ₽';
    tariffPremium.textContent = '8 990 ₽';
    tariffsDiscountNotification.classList.add('active');
  } else {
    tariffBusiness.textContent = '4 990 ₽';
    tariffPremium.textContent = '9 990 ₽';
    tariffsDiscountNotification.classList.remove('active');
  }
}
