import {feedbackRequestHandler} from "./request-handler";
import {closePopup} from "./main";

export const formHandler = function (formHtmlElement) {

  const emailRegExp = /.+@.+\..+/i;

  const phoneRegExp = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/;

  const nameInputEl = () => formHtmlElement.querySelector('#name');

  const contactInputEl = () => formHtmlElement.querySelector('#contact');

  const agreementInputEl = () => formHtmlElement.querySelector('#agreement');

  const submitButtonEl = () => formHtmlElement.querySelector('#submit');

  const isFormValid = () => emailRegExp.test(contactInputEl().value.trim()) || phoneRegExp.test(contactInputEl().value.trim());

  const submitButtonHandler = (evt) => {
    evt.preventDefault();
    submitButtonEl().disabled = true;
    submitButtonEl().innerHTML = 'Отправляем...';
    feedbackRequestHandler().submit({
      name: nameInputEl().value.trim(),
      email: emailRegExp.test(contactInputEl().value.trim()) ? contactInputEl().value.trim() : null,
      phone: phoneRegExp.test(contactInputEl().value.trim()) ? contactInputEl().value.trim() : null
    }, (result) => {
      if (result['data']['contact']['sendTelegramFeedback']['success']){
        submitButtonEl().innerHTML = 'Отправлено!';
        closePopup(formHtmlElement);
      } else {
        submitButtonEl().disabled = false;
        submitButtonEl().innerHTML = 'Оставить заявку';
      }
    })
  }

  return {
    init: () => {
      if (formHtmlElement.id === 'entity'){
        return
      }
      submitButtonEl().disabled = true;
      contactInputEl().classList.add('invalid');
      contactInputEl().oninput = () => {
        contactInputEl().classList[isFormValid() ? 'remove' : 'add']('invalid');
        submitButtonEl().disabled = !isFormValid();
      };
      agreementInputEl().oninput = evt => submitButtonEl().disabled = !evt.target.checked;
      submitButtonEl().onclick = evt => submitButtonHandler(evt);
    },
    clear: () => {
      if (formHtmlElement.id === 'entity'){
        return
      }
      nameInputEl().value = null;
      contactInputEl().value = null;
      agreementInputEl().checked = true;
    }
  }
}
